.login-wrapper {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:100%;
    width:100%;
    margin-top: 300px;
    border-radius: 50%;
}

.login {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap:2rem;
    gap:2rem;
    padding: 5rem;
    padding-bottom: 2rem;
}


.table {
    background-color: rgb(250, 250, 250);
    width: auto;
    height: auto;
    padding:01em;
}
td{
    font-size: 14px;
}
.filter{
    background-color: rgb(250, 250, 250);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;  
    width: 15%;
    padding-block: 1em;
    border-radius:8px;
    box-shadow: 0px 1px 8px #888888;
}

.header{
    padding: 0rem;
    margin: 30px;
}



.home-wrapper{
    display: flex;
    justify-content: space-around;
    grid-gap: 00%;
    gap: 00%;
    padding: 2em;
}

.column-one{
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    grid-gap: 30px;
    gap: 30px;
}

.problemset-wrapper{
    text-align: left;
}

.column-two{
    background-color: rgb(250, 250, 250);
    border-radius: 8px;
    box-shadow: rgb(136 136 136) 0px 1px 12px;
    width: 20%;
    margin-top: 80px;
    margin-right: 100px;
}

.links-home{
    color: #007bff
}

.links-home:hover{
    text-decoration: underline;
    color:#0056b3;
}


.register-wrapper {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:100%;
    width:100%;
    margin-top: 400px;

}

.register {
    display:flex;
    flex-direction: column;
    padding:2rem;
}
.problem-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.problem-header{
    background-color: #343a40;
    padding: 1.4rem;
    margin-top: 100px;
    width: 90%;
    color:'#fafafa';
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.statment{
    background-color: #fafafa;
    width:90%;
    padding: 2em;
}

.samples{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.editor-box {
    display:flex;
    flex-direction: row;
    align-items: center;
    padding:2.5rem
}

.section {
    padding:1rem;
    border:1px solid black;
}

.form {
    display:flex;
    grid-gap:1rem;
    gap:1rem;
}

.input {
    font-size: large;
}

.editor-wrapper {
    flex:1 1;
    padding:15px;
}

.preview-wrapper {
    flex:1 1;
    padding:15px;
    overflow-y: hidden;
}

.preview {
    overflow-y: scroll;
    max-height: 500px;
    height: 500px;
}

.wrap {
    display:flex;
    flex-direction: column;
    grid-gap:1rem;
    gap:1rem;
    padding:2.5rem;
}
.container {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: rgb(250 250 250);
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 8px;
    box-shadow: 0px 1px 10px #888888;
    width: 90%;
}

.vertical {
    border-left: 2px solid #4d4b4b;
    height: 300px;
    margin-bottom: 8px
}

input {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 3px solid #ccc;
    transition: 0.5s;
    outline: none;
  }

.all-problems-wrapper {
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.all-problems {
    min-height: calc(100% - 55px);
    max-height: calc(100% - 55px);
}
.create-blog-wrapper {
    display: flex;
    flex-direction: column;
    padding:1rem;
}

.create-blog {
    display:grid;
    height:90%;
    grid-template-columns: 50% 50%;
}

.controls {
    padding:1rem;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.brand {
    font-size: 25px;
    margin-left: 10px;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fafafa
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 65px;
    background-color: #343a40;
    padding:1em
}

.links a {
    margin-right: 2em;
    transition: -webkit-text-decoration 0.5s ease-in;
    transition: text-decoration 0.5s ease-in;
    transition: text-decoration 0.5s ease-in, -webkit-text-decoration 0.5s ease-in;
    display: inline-block;
    color:#ffffff;
    font-size: 22px;
}

.links a::after {
    content: '';
    width: 0px;
    height: 1px;
    display: block;
    background: #fafafa;
    margin-top: 2px;
    transition: 300ms;
}

.links a:hover::after {
    width: 100%;
}

.profile {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logout{
    transition: -webkit-text-decoration 0.5s ease-in;
    transition: text-decoration 0.5s ease-in;
    transition: text-decoration 0.5s ease-in, -webkit-text-decoration 0.5s ease-in;
    display: inline-block;
    color:#fafafa;
    font-size: 23px;

}
.logout::after {
    content: '';
    width: 0px;
    height: 1px;
    display: block;
    background: #fafafa;
    margin-top: 2px;
    transition: 300ms;
}

.logout:hover::after {
    width: 100%;
}
*, *::after, *::before {
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    padding: 0;
    margin: 0;
}

html, body, #root {
    height:100%;
    width:100%;
}

#root {
    display:grid;
    grid-template-rows: 55px calc(100% - 55px);
}

a{
    text-decoration: none;
    color:inherit;
    font-size: larger;
}

.flex-row-gap {
    display:flex;
    flex-direction: row;
    grid-gap:1rem;
    gap:1rem;
}

.flex-col-gap {
    display:flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.align-center {
    align-items: center;
}

.pagination-controls {
    justify-content: space-between;
    padding:1rem;
    margin: 1rem 0rem;
}
